import React, { useEffect } from 'react'
import { useState } from 'react'
import { Button, IconButton, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TextField, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core';
import { Build, Delete } from '@material-ui/icons';
import { format } from 'date-fns';
import { Link } from 'react-router-dom';
import { v4 as uuid } from 'uuid';


const useStyles = makeStyles({
    taulukko : {
      width: 750,
      border: "solid black 2px",
      boxShadow: "0px 0px 3px black",
      padding: 20,
      backgroundColor:"lightblue"
    }
  })

function Velkalista() {

    const tyylit = useStyles();
  
    //Velkalistan muuttujat
    const [summa, setSumma] = useState();
    const [nimi, setNimi] = useState();
    const [pvm, setPvm] = useState(new Date().getTime())
    const [kello, setKello] = useState(new Date())
    // const [summienSumma, setsummienSumma] = useState();
    const [summaBeta, setSummaBeta] = useState([]);
  
    //Arrayt
    const [velalliset, setVelalliset] = useState([]);
  
  //Velkalistan funktiot
  let uusiHenkilo = {
                    id : uuid(),
                    lisayspaiva : pvm,
                    henkilonnimi : nimi,
                    rahasumma : Number(summa)
  }

  // let sum = 0 + Number(summa); //Alustus for-looppia varten

    const lisaaVelallinen = () => {
    setVelalliset([...velalliset, uusiHenkilo]);
    setSummaBeta([...summaBeta, uusiHenkilo.rahasumma]);
    // setsummienSumma(summa)
    // for(let i = 0;i < velalliset.length;i++){
    //     sum += velalliset[i].rahasumma;
    //     setsummienSumma(sum)
    //     }
    }

    const poista = (indeksi) => {
    velalliset.splice(indeksi, 1)
    summaBeta.splice(indeksi, 1)
    // for(let i = 0;i < velalliset.length;i++){
    //     sum += velalliset[i].rahasumma;
    //     setsummienSumma(sum)
    //     }
    // setsummienSumma(sum - Number(summa));
    setVelalliset([...velalliset])
    setSummaBeta([...summaBeta])
    }

    useEffect(() => {
      if ( (localStorage.getItem("velalliset")) && (localStorage.getItem("summaBeta")) ) {
        setVelalliset(JSON.parse(localStorage.getItem("velalliset")))
        setSummaBeta(JSON.parse(localStorage.getItem("summaBeta")))
      }
      else {
        setVelalliset([])
        setSummaBeta([])
      }
    }, [])

    useEffect(() => {
      setKello(new Date())
      setPvm(new Date().getTime())
    }, [kello])


    useEffect(() => {
      localStorage.setItem("velalliset", JSON.stringify(velalliset))
      localStorage.setItem("summaBeta", JSON.stringify(summaBeta))
    },[velalliset, summaBeta])

    return (
        <div style={{width:"750px", margin:"auto"}}>
    <TableContainer className={tyylit.taulukko}>
      <Typography variant="h2" style={{textAlign:"center", fontWeight:"bold"}}>Velat</Typography>
      <Table>
        <TableHead>
          <TableRow>
          <TableCell><Typography variant="h5" style={{fontWeight:"bold"}}>Lisäyspäivä</Typography></TableCell>
            <TableCell><Typography variant="h5" style={{fontWeight:"bold"}}>Kenelle</Typography></TableCell>
            <TableCell><Typography variant="h5" style={{fontWeight:"bold"}}>Summa</Typography></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
        {velalliset.map( (velallinen, idx) => {
              return (
        <TableRow key={idx}>
          
          <TableCell>{format(velallinen.lisayspaiva, "d.M.y HH:mm")}</TableCell>
          <TableCell>{velallinen.henkilonnimi}</TableCell>
          <TableCell>{velallinen.rahasumma} €</TableCell>
          
          <IconButton onClick={ () => {
            poista(idx);
          }}><Delete/></IconButton>

          <IconButton
          component={Link}
          to={`/muokkaa/${velallinen.id}`}
          ><Build/></IconButton>
        </TableRow>
        )
        })}
        <TableRow>
          <TableCell>
          {format(kello, "d.M.y HH:mm:ss")}
          </TableCell>

          <TableCell>

            <TextField 
            type="text" 
            placeholder="Kenelle" 
            onChange={ (e) => { setNimi(e.target.value) } }
            />
            </TableCell>

          <TableCell>

            <TextField 
            type="number" 
            placeholder="Summa (€)" 
            onChange={ (e) => { setSumma(e.target.value ) } } 
            />
            </TableCell>

          <TableCell>

            <Button 
            onClick={lisaaVelallinen}>
              Lisää velka
            </Button>
          </TableCell>
        </TableRow>
        </TableBody>
      </Table>
      { ((summaBeta === undefined) && (summaBeta.length > 0))
        ? <Typography 
          variant="h4"
          style={{textAlign:"center", marginTop:20}}
        >Velkasi on <b>0</b> euroa.</Typography>

        : <Typography 
          variant="h4"
          style={{textAlign:"center", marginTop:20}}
        >Velkasi on <b>
        {
         (summaBeta.length > 0)
         ? summaBeta.reduce((summa, raha) => {
          return (summa + raha)})
         : 0
         }</b> euroa.</Typography>
      }
    </TableContainer>
        </div>
    )
}

export default Velkalista;
