import { Container, makeStyles, TextField, Typography } from '@material-ui/core'
import { DatePicker } from '@material-ui/pickers';
import React, { useState } from 'react'
import { useParams } from 'react-router';

const useStyles = makeStyles({
    taulukko : {
      width: 750,
      border: "solid black 2px",
      boxShadow: "0px 0px 3px black",
      padding: 20,
      backgroundColor:"lightgray"
    },
    teksti : {
        backgroundColor: "white",
        marginTop : 10
    }
  })

function Muokkaa(props) {

    const { id } = useParams();

    const tyylit = useStyles()

    const [pvm, setPvm] = useState(new Date())
    const [nimi, setNimi] = useState(props.velalliset.filter((velallinen) => {
        return (velallinen.id === id)})[0].henkilonnimi);
    const [summa, setSumma] = useState(props.velalliset.filter((velallinen) => {
        return (velallinen.id === id)})[0].rahasumma);

    return (
        <Container className={tyylit.taulukko}>

            <Typography 
            variant="h2" 
            style={{textAlign:"center", fontWeight:"bold"}}
            >Muokkaa</Typography>

            <TextField
            className={tyylit.teksti}
            value={nimi}
            variant="outlined"
            fullWidth
            label="Muokkaa nimeä"
            placeholder="Nimi"
            />

            <TextField
            className={tyylit.teksti}
            variant="outlined"
            fullWidth
            label="Muokkaa nimeä"
            placeholder="Nimi"
            />

            <DatePicker
            fullWidth
            inputVariant="outlined"
            format="d.M.y HH:mm"
            label="Muokkaa päivämäärää"
            value={pvm}
            onChange={setPvm}
            />

        </Container>
    )
}

export default Muokkaa
