import { AppBar, CssBaseline, Toolbar, Typography } from "@material-ui/core";
import { Assignment, ImportContacts } from "@material-ui/icons";
import HomeIcon from '@material-ui/icons/Home';
import { Link } from 'react-router-dom';

function Navigaatio() {

    return(
        <CssBaseline>
        <AppBar position="static" color="primary" style={{marginBottom: "20px"}}>
            <Toolbar>

                <Typography 
                variant="h3" 
                edge="start"
                style={{marginRight:"0px"}}
                >Velat ja saatavat</Typography>

                <div style={{margin:"auto"}}>
                    <Typography
                    component={Link}
                    to="/"
                    variant="h6" 
                    display="inline" 
                    style={{marginLeft:"20px", color:"white", textDecoration:"none"}}
                    >
                    <HomeIcon fontSize="inherit" style={{marginRight:"5px"}}/>
                        Velkalista
                    </Typography>

                    <Typography
                    component={Link}
                    to="/muokkaa"
                    variant="h6" 
                    display="inline" 
                    style={{marginLeft:"20px", color:"white", textDecoration:"none"}}
                    >
                    <HomeIcon fontSize="inherit" style={{marginRight:"5px"}}/>
                        Muokkaa
                    </Typography>
                </div>
            </Toolbar>
        </AppBar>
        </CssBaseline>
    );
}

export default Navigaatio