import React from 'react';
import { HashRouter as Router, Route } from 'react-router-dom';
import Muokkaa from './component/Muokkaa';
import SimpleTabs from './component/TabPanel';
import Navigaatio from './component/Valikko';
import Velkalista from './component/Velkalista';

function App() {

  return (
    <Router>

      <Navigaatio/>

      <Route path="/" exact component={Velkalista}>
      <Velkalista/>
      </Route>

      <Route path="/muokkaa/:id" exact component={Muokkaa}>
        <Muokkaa/>
      </Route>

    </Router>
  );
}

export default App;